* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  color: inherit;
  box-sizing: border-box;
  background-color: transparent;
}

*:focus {
  outline: none;
}

:root {
  --primary: #522398;
  --secondary: #754db1;
  --light-grey: #ddd;
  --placeholder: #a188ca;
}

body {
  overflow-y: hidden;
  touch-action: none; /* removes pinch zoom */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

input,
.allowSelect {
  -webkit-touch-callout: all; /* iOS Safari */
  -webkit-user-select: all; /* Safari */ /* Konqueror HTML */
  -moz-user-select: all; /* Old versions of Firefox */
  -ms-user-select: all; /* Internet Explorer/Edge */
  user-select: all; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-transition: all 0s 50000s;
  transition: all 0s 50000s; /* this removes the background from autofill - I don't know why it works */
}

